.count__wrapper {
    display: grid;
    grid-template-columns: 60% 40%;
    gap: 80px 0;
    margin-bottom: 100px;

    @include mediaTablet {
        grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    }
}

.price__wrapper {
    display: grid;
    gap: 80px 50px;
}

.price {
    position: relative;
    height: 35px;
}

.text_price {
    position: absolute;
    left: 30px;
    top: 0;
    margin: 5px 0;
    font-size: 13px;
    pointer-events: none;
}

.count__mortage {
    overflow-x: hidden;
    font-family: $font-secondary;
    width: 100%;
    height: 35px;
    margin-top: 3px;
    background-color: rgba(95, 95, 95, 0.5);
    color: $white;
    padding: 30px 27px;
    border-radius: 10px;
    border-left: 3px double transparent;
    border-top: 3px double transparent;
    border-right: 3px double transparent;
    transition: all 0.3s ease-in;
    font-size: 17px;

    &:hover {
        border-left: 3px double $accent;
        border-top: 3px double $accent;
        border-right: 3px double $accent;
        background: rgba(95, 95, 95, 0.3);
    }

}

.mortage__range {
    position: absolute;
    left: 0;
    bottom: -32px;
    width: 100%;
    height: 12.5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: $white;
    border-radius: 0 0 15px 15px;
    background-image: linear-gradient($accent, $accent);
    background-repeat: no-repeat;
    transition: all 1s ease;

    &::-webkit-slider-thumb {
        position: relative;
        left: 0;
        
        width: 25px;
        height: 25px;
        border-radius: 50%;
        -webkit-appearance: none;
        background: $white;
        border: 2px solid transparent;
        transition: all 0.2s ease-in;

        &:hover {
            cursor: pointer;
            background-color: $accent;
            border: 2px solid $dark;
        }
    }

}

.count__price__info {
    position: absolute;
    top: 17px;
    right: 14px;
    color: $accent;
    font-size: 20px;
    pointer-events: none;
}

.wrapper__price__info {
    display: flex;
    justify-content: space-between;
    padding: 10px 5px;
}

.info__mortgage {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 25px 0 25px 25px;

    @include mediaTablet {
        grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
        align-items: center;
        text-align: center;
        padding: 0;
    }

    .info:nth-child(2) .price__info {
        color: $accent;
    }

    .info {
        padding: 10px 15px;
        font-size: 14px;
        letter-spacing: 1.5px;
    }

    .text__info {
        margin-bottom: 20px;
    }

    .price__info {
        font-size: 22px;
        font-family: $font-secondary;
    }
}