.card {
    position: relative;
    display: inline-block;
    overflow: hidden;
    max-width: 540px;
    max-height: 320px;

    &::before {
        content: "";
        position: absolute;
        z-index: 6;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(36, 43, 51, 0) 0%, $dark 100%);
    }

    &::after {
        content: "";
        opacity: 0;
        position: absolute;
        z-index: 7;
        left: 15px;
        top: 15px;
        right: 15px;
        bottom: 15px;
        border: 3px double rgba(255, 255, 255, 0.4);
        transition: opacity 0.2s ease-in;
    }

    &:hover::after {
        opacity: 1;
    }
}

.card__img {
    position: relative;
    background-position: center;
    z-index: 5;
    transition: all 0.3s ease-in;
    height: 100%;

}

.card:hover .card__img {
    transform: scale(1.1);
    filter: contrast(110%) brightness(1.2) saturate(1.2);
}

.card__title {
    position: absolute;
    z-index: 9;
    left: 50px;
    right: 20px;
    bottom: 44px;
    font-size: 24px;

    @include mediaTablet {
        font-size: 20px;
        left: 30px;
        bottom: 30px;
    }
}