.district {
    position: relative;

    &::before {
        content: "III";
        right: calc(50% + 555px);
        transform: translate(50%, 0%);

        @include decorNumber;
    }
}

.ourSpecialist {
    margin-top: 80px;
    letter-spacing: 2px;
}

.district__title {
    margin-bottom: 88px;

    @include mediaTablet {
        margin-bottom: 44px;
    }
}

.appartments__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include mediaTablet {
        grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    }
}