.video {
    overflow: hidden;
    position: relative;
    text-align: center;

    &::after {
        content: "IV";
        left: calc(50% + 555px);
        transform: translate(-50%, 0%);

        @include decorNumber;
    }

}

.video__link {
    position: relative;
    display: inline-block;
    text-align: center;

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(36, 43, 51, 0.8);
        transition: background-color 0.3s ease-in;
    }

    &:hover::before {
        background: rgba(36, 43, 51, 0.55);
    }
}

.video__icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    transition: all 0.3s ease-in;

    @include mediaTablet {
        width: 80px;
        height: 80px;
    }
}

.video__link:hover .video__icon {
    transform: translate(-50%, -50%) scale(1.1);
    filter: saturate(1.5);
}