.modal {
    z-index: 15;
    height: 90vh;
    width: 100%;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 20px;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    padding: 20px 20px;
    pointer-events: none;
    transition: opacity 0.5s ease-in-out;
    border: 4px double transparent;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal__content {
    text-align: center;
    padding: 20px;
    border-radius: 12px;
    background: $dark;
    border: 4px double $accent;
    transform: scale(0.5);
    transition: all 0.5s ease-in-out;
}

.modal__content.active {
    transform: scale(1);
}

.modal__text {
    padding: 20px 0 0 0;
}

.modal__title {
    text-align: center;
    letter-spacing: 0.2;
}

.modal__price {
    font-size: 20px;
    margin-top: 15px;
    color: $accent;
}

.close {
    width: 30px;
    height: 30px;
    z-index: 99;
}

.link__toCatalogy {
    width: 100%;
    height: 50px;
    font-size: 16px;
    color: $white;
    font-weight: 400;
    text-align: center;
    padding: 12px;
    background-color: $accent;
    transition: background-color 0.2s ease-in;


    &:hover {
        background-color: saturate($accent, 30%);
        color: #000;
    }
}