.category__select {
    padding-bottom: 200px;
}

.category__filter {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    margin-bottom: 42px;
    gap: 15px;
}

.category__text {
    margin-bottom: 15px;
    color: $accent;
}

.filter__room ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, 60px);
    text-align: center;
    gap: 10px;
}

.room {
    cursor: pointer;
    border: 1px solid $white;
    font-family: $font-secondary;
    padding: 5px;
    transition: all 0.2s ease-in;

    &:hover {
        background-color: $accent;
        border-color: $dark;
        color: $dark;
    }

    &.room__active {
        background-color: $accent;
        border-color: $white;
        color: $dark;
    } 
}

.filter__district ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    text-align: center;
}

.district__item {
    padding: 10px;
    cursor: pointer;
    border: 1px solid transparent;
    border-bottom: 1px solid $white;
    transition: all 0.2s ease-in;

    &:hover {
        border: 1px solid $white;
        color: $accent;
    }

    &.active {
        border: 1px solid $accent;
        color: $accent;
    }
}